.descriptionBlock {
  padding: 10px;
  display: flex;
  position: relative;
}
.photo img {
  height: 200px;
  width: 180px;
  border-radius: 30px;
}
.photo input {
  display: none;
  position: absolute;
}
.photo  label{
  position: absolute;
  background-color: black;
  opacity: 0;
  width: 180px;
  padding: 5px 0;
  top: 180px;
  left: 10px;
  border-radius: 0 0 30px 30px;
  text-align: center;
  color: white;
}
.photo:hover label {
  opacity: 50%;
}
.editProfileBtn {
  cursor: pointer;
}
.editModeButton {
  position: absolute;
  text-align: center;
  width: 150px;

}
.pageTop {
  padding: 5px;
  border-bottom: 1px solid black;
  width: 500px;
}
.rightBlock {
  margin-left: 30px;
  padding: 5px;
  border-bottom: 1px solid black;
}
.pageBottom {
  padding-top: 20px;
}
.name {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 10px;
}
.contacts {
  padding-left: 20px;
}


.editModeButton:hover {
  background-color: #edeef0;
  border-radius: 10px;
}
.editModeButton img {
  height: 15px;
  width: 15px;
}
.formSummaryError {
  border: 1px solid red;
  padding: 5px;
  color: red;
}
.item {
  margin-bottom: 5px;
}